.content {
  position: relative;
  width: 90%;
  max-width: 400px;
  margin: auto;
  overflow: hidden;
}
.overlay {
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  /* -webkit-transition: all 0.4s ease-in-out 0s;
  -moz-transition: all 0.4s ease-in-out 0s; */
  transition: all 0.4s ease-in-out 0s;
}

.content:hover .overlay {
  opacity: 1;
}

.contentDetails {
  position: absolute;
  text-align: center;
  padding-left: 1em;
  padding-right: 1em;
  width: 100%;
  top: 50%;
  left: 50%;
  opacity: 0;
  /* -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%); */
  transform: translate(-50%, -50%);
  /* -webkit-transition: all 0.3s ease-in-out 0s;
  -moz-transition: all 0.3s ease-in-out 0s; */
  transition: all 0.4s ease-in-out 0s;
}

.content:hover .contentDetails {
  top: 50%;
  left: 50%;
  opacity: 1;
  color: #fff;
}

.content:hover .contentTitle {
  color: #fff;
  font-weight: 500;
  margin-bottom: 0.5em;
  text-transform: uppercase;
}

.contentText {
  color: #fff;
  font-size: 0.8em;
}

.fadeInBottom {
  top: 80%;
}
